import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import { useState } from 'react';
import eyeFill from '@iconify/icons-eva/eye-fill';
import closeFill from '@iconify/icons-eva/close-fill';
import shareFill from '@iconify/icons-eva/share-fill';
import downloadFill from '@iconify/icons-eva/download-fill';
import { PDFDownloadLink, PDFViewer } from '@react-pdf/renderer';
// material
import { Box, Tooltip, IconButton, DialogActions, Stack, Button } from '@mui/material';
import { LoadingButton } from '@mui/lab';
//
import { DialogAnimate } from '../../../animate';
import InvoicePDF from './InvoicePDF';
import InvoiceAgentPDF from './InvoiceAgentPDF';
import { fDateTime } from '../../../../utils/formatTime';

// ----------------------------------------------------------------------

InvoiceToolbar.propTypes = {
  invoice: PropTypes.object.isRequired,
  infotirages: PropTypes.object.isRequired,
  agents: PropTypes.object.isRequired,
  surcussales: PropTypes.object.isRequired,
  value: PropTypes.string.isRequired,
  value1: PropTypes.string.isRequired,
  value2: PropTypes.object.isRequired,
  value3: PropTypes.string.isRequired,
  value4: PropTypes.string.isRequired,
  tirage: PropTypes.string.isRequired,
  handleInvoice: PropTypes.func.isRequired,
  companyname: PropTypes.string,
  userPhoto: PropTypes.string,
  isAgent: PropTypes.bool.isRequired
};

export default function InvoiceToolbar({
  invoice,
  handleInvoice,
  value,
  value1,
  value2,
  value3,
  value4,
  companyname,
  agentName,
  numeroMachine,
  tirage,
  isAgent = false,
  userPhoto,
  infotirages,
  agents,
  surcussales
}) {
  const [openPDF, setOpenPDF] = useState(false);

  const handleOpenPreview = () => {
    setOpenPDF(true);
  };

  const handleClosePreview = () => {
    setOpenPDF(false);
  };

  return (
    <>
      <Stack mb={5} direction="row" justifyContent="flex-end" spacing={1.5}>
        <Button color="error" size="small" variant="contained" onClick={handleInvoice}>
          Annuler
        </Button>

        <Button
          color="info"
          size="small"
          variant="contained"
          onClick={handleOpenPreview}
          endIcon={<Icon icon={eyeFill} />}
          sx={{ mx: 1 }}
        >
          Aperu
        </Button>

        <PDFDownloadLink
          document={
            !isAgent ? (
              <InvoicePDF
                invoice={invoice}
                value={value}
                value1={value1}
                value2={value2}
                value3={value3}
                value4={value4}
                infotirages={infotirages}
                agents={agents}
                surcussales={surcussales}
                userPhoto={userPhoto}
                tirage={tirage}
              />
            ) : (
              <InvoiceAgentPDF
                invoice={invoice}
                value={value}
                value1={value1}
                agentName={agentName}
                numeroMachine={numeroMachine}
                userPhoto={userPhoto}
                tirage={tirage}
              />
            )
          }
          fileName={`Rapport de vente le ${value} a ${value1}`}
          style={{ textDecoration: 'none' }}
        >
          {({ loading }) => (
            <LoadingButton
              size="small"
              loading={loading}
              variant="contained"
              loadingPosition="end"
              endIcon={<Icon icon={downloadFill} />}
            >
              Tlcharger
            </LoadingButton>
          )}
        </PDFDownloadLink>
      </Stack>

      <DialogAnimate fullScreen open={openPDF}>
        <Box sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
          <DialogActions
            sx={{
              zIndex: 9,
              padding: '12px !important',
              boxShadow: (theme) => theme.customShadows.z8
            }}
          >
            <Tooltip title="Close">
              <IconButton color="inherit" onClick={handleClosePreview}>
                <Icon icon={closeFill} />
              </IconButton>
            </Tooltip>
          </DialogActions>
          <Box sx={{ flexGrow: 1, height: '100%', overflow: 'hidden' }}>
            <PDFViewer width="100%" height="100%" style={{ border: 'none' }}>
              {!isAgent ? (
                <InvoicePDF
                  invoice={invoice}
                  value={value}
                  value1={value1}
                  value2={value2}
                  value3={value3}
                  value4={value4}
                  companyname={companyname}
                  userPhoto={userPhoto}
                  tirage={tirage}
                  infotirages={infotirages}
                  agents={agents}
                  surcussales={surcussales}
                />
              ) : (
                <InvoiceAgentPDF
                  invoice={invoice}
                  value={value}
                  value1={value1}
                  companyname={companyname}
                  agentName={agentName}
                  numeroMachine={numeroMachine}
                  userPhoto={userPhoto}
                  tirage={tirage}
                />
              )}
            </PDFViewer>
          </Box>
        </Box>
      </DialogAnimate>
    </>
  );
}
