import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
// material
import { useTheme, styled } from '@mui/material/styles';
import {
  Stack,
  Tooltip,
  IconButton,
  Button,
  TextField,
  MenuItem,
  Select,
  Box,
  Chip,
  Checkbox,
  OutlinedInput,
  ListItemText
} from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { DatePicker } from '@mui/x-date-pickers';

// ----------------------------------------------------------------------

RapportListToolbar.propTypes = {
  value: PropTypes.string,
  value1: PropTypes.string,
  value2: PropTypes.string,
  value3: PropTypes.string,
  value4: PropTypes.string,
  onChangeDate1: PropTypes.func,
  onChangeDate2: PropTypes.func,
  handleChangeMultiple: PropTypes.func,
  setValue3: PropTypes.func,
  setValue4: PropTypes.func,
  handleGo: PropTypes.func,
  handleInvoice: PropTypes.func,
  isRapportNotFound: PropTypes.bool,
  loading: PropTypes.bool,
  agents: PropTypes.arrayOf(PropTypes.object),
  surcussales: PropTypes.arrayOf(PropTypes.object),
  infoTirages: PropTypes.arrayOf(PropTypes.object)
};

const INPUT_WIDTH = 200;

export default function RapportListToolbar({
  onChangeDate1,
  onChangeDate2,
  isRapportNotFound,
  handleGo,
  handleInvoice,
  value,
  value1,
  value2,
  value3,
  value4,
  handleChangeMultiple,
  setValue3,
  setValue4,
  loading,
  agents,
  surcussales,
  infoTirages
}) {
  const role = window.localStorage.getItem('role');

  return (
    <Stack spacing={2} direction={{ xs: 'column', md: 'row' }} sx={{ py: 2.5, px: 3 }}>
      <TextField
        fullWidth
        select
        label="Tirage"
        SelectProps={{
          MenuProps: {
            sx: { '& .MuiPaper-root': { maxHeight: 260 } }
          },
          multiple: true,
          value: value2,
          onChange: handleChangeMultiple,
          renderValue: (selected) => (
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
              {selected.map((tirageId) => (
                <Chip key={tirageId} label={infoTirages?.find((e) => e.id === tirageId)?.name} />
              ))}
            </Box>
          )
        }}
        sx={{
          maxWidth: { md: INPUT_WIDTH },
          textTransform: 'capitalize'
        }}
      >
        {infoTirages?.map((n) => (
          <MenuItem key={n.id} value={n.id}>
            <Checkbox checked={value2?.includes(n.id)} />
            <ListItemText primary={n.name} />
          </MenuItem>
        ))}
      </TextField>

      {role !== 'supervisor' && (
        <TextField
          fullWidth
          select
          label="Surcussale"
          value={value3}
          onChange={(event) => setValue3(event.target.value)}
          SelectProps={{
            MenuProps: {
              sx: { '& .MuiPaper-root': { maxHeight: 260 } }
            }
          }}
          sx={{
            maxWidth: { md: INPUT_WIDTH },
            textTransform: 'capitalize'
          }}
        >
          {[{ id: 0, addresse_complet: 'Tout' }, ...surcussales]?.map((option) => (
            <MenuItem
              key={option?.id}
              value={option?.id}
              sx={{
                mx: 1,
                my: 0.5,
                borderRadius: 0.75,
                typography: 'body2',
                textTransform: 'capitalize'
              }}
            >
              {option?.addresse_complet}
            </MenuItem>
          ))}
        </TextField>
      )}

      <DatePicker
        label="Date de dbut"
        value={value}
        inputFormat="yyyy-MM-dd"
        onChange={(newValue) => {
          onChangeDate1(newValue);
        }}
        renderInput={(params) => (
          <TextField
            {...params}
            fullWidth
            sx={{
              maxWidth: { md: INPUT_WIDTH }
            }}
          />
        )}
      />

      <DatePicker
        label="Date de fin"
        value={value1}
        inputFormat="yyyy-MM-dd"
        onChange={(newValue) => {
          onChangeDate2(newValue);
        }}
        renderInput={(params) => (
          <TextField
            {...params}
            fullWidth
            sx={{
              maxWidth: { md: INPUT_WIDTH }
            }}
          />
        )}
      />

      <Tooltip title="Filter list">
        <LoadingButton loading={loading} variant="contained" onClick={handleGo}>
          Filtrer
        </LoadingButton>
      </Tooltip>
      {!isRapportNotFound && (
        <Tooltip title="Effectuer Rapport">
          <LoadingButton loading={loading} variant="contained" color="secondary" onClick={handleInvoice}>
            Rapport
          </LoadingButton>
        </Tooltip>
      )}
    </Stack>
  );
}
