import React, { useState } from 'react';
import Rapport from './RapportComp';
import Invoice from './RapportInvoice';

export default function Rapports() {
  const [invoiceActive, setInvoiceActive] = useState(false);
  const [value, setValue] = useState(new Date());
  const [value1, setValue1] = useState(new Date());
  const [value2, setValue2] = useState(0);
  const [value3, setValue3] = useState(0);
  const [value4, setValue4] = useState(0);
  const [value5, setValue5] = useState(1);
  const [agent, setAgentName] = useState('');
  const [numeroMachine, setNumeroMachine] = useState('');
  const [selTirage, setSelTirage] = useState('Tout');
  const handleInvoice = () => {
    setInvoiceActive(!invoiceActive);
  };

  return (
    <>
      {invoiceActive === false ? (
        <Rapport
          handleInvoice={handleInvoice}
          value={value}
          value1={value1}
          value2={value2}
          value3={value3}
          value4={value4}
          value5={value5}
          setValue={setValue}
          setValue1={setValue1}
          setValue2={setValue2}
          setValue3={setValue3}
          setValue4={setValue4}
          setValue5={setValue5}
          setAgentName={setAgentName}
          agent={agent}
          numeroMachine={numeroMachine}
          setNumeroMachine={setNumeroMachine}
          setSelTirage={setSelTirage}
          selTirage={selTirage}
        />
      ) : (
        <Invoice
          handleInvoice={handleInvoice}
          value={value}
          value1={value1}
          value2={value2}
          value3={value3}
          value4={value4}
          value5={value5}
          setValue={setValue}
          setValue1={setValue1}
          setValue2={setValue2}
          setValue3={setValue3}
          setValue4={setValue4}
          setValue5={setValue5}
          setAgentName={setAgentName}
          agent={agent}
          numeroMachine={numeroMachine}
          setNumeroMachine={setNumeroMachine}
          setSelTirage={setSelTirage}
          tirage={selTirage}
        />
      )}
    </>
  );
}
