import { v4 as uuidv4 } from 'uuid';
import { random, sum } from 'lodash';
// material
import { styled, useTheme } from '@mui/material/styles';
import {
  Box,
  Grid,
  Card,
  Table,
  Divider,
  TableRow,
  Container,
  TableBody,
  TableHead,
  TableCell,
  Typography,
  TableContainer
} from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../../redux/store';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
// utils
import { fCurrency } from '../../../utils/formatNumber';
// hooks
import useSettings from '../../../hooks/useSettings';
// components
import Page from '../../../components/Page';
import Label from '../../../components/Label';
import Scrollbar from '../../../components/Scrollbar';
import HeaderBreadcrumbs from '../../../components/HeaderBreadcrumbs';
import { InvoiceToolbar } from '../../../components/_dashboard/rapport/invoice';
import { fDateTime, fDate } from '../../../utils/formatTime';
import useAuth from '../../../hooks/useAuth';
// ----------------------------------------------------------------------

const RowResultStyle = styled(TableRow)(({ theme }) => ({
  '& td': {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1)
  }
}));

// ----------------------------------------------------------------------

export default function RapportInvoice({
  handleInvoice,
  value,
  value1,
  value2,
  value3,
  value4,
  value5,
  agent,
  numeroMachine,
  tirage
}) {
  const { themeStretch } = useSettings();
  const { user } = useAuth();
  const theme = useTheme();

  const today = fDateTime(new Date());

  const { rapportagents } = useSelector((state) => state.user);
  const total = sum(rapportagents?.data?.map((item) => parseFloat(item.gain))).toFixed(2);
  const total1 = sum(rapportagents?.data?.map((item) => parseFloat(item.perte))).toFixed(2);
  const total2 = sum(rapportagents?.data?.map((item) => parseFloat(item.commission))).toFixed(2);
  const total3 = sum(rapportagents?.data?.map((item) => parseFloat(item.montant_total))).toFixed(2);
  const total4 = sum(rapportagents?.data?.map((item) => item.quantite));
  const total5 = sum(rapportagents?.data?.map((item) => item.fgagnant));
  const totalretrait = sum(rapportagents?.data?.map((item) => item.retrait)).toFixed(2);
  const totaldepot = sum(rapportagents?.data?.map((item) => item.depot)).toFixed(2);
  const totalbalance = sum(rapportagents?.data?.map((item) => item.balance)).toFixed(2);

  const handleDate = (dateString) => {
    const date = new Date(dateString);
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const formattedDate = `${year}-${month}-${day}`;
    return formattedDate;
  };

  return (
    <Page title="Rapport: Details | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Rapports Agent Details"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            {
              name: 'Rapport Agent',
              href: PATH_DASHBOARD.rapport
            },
            { name: 'Details' }
          ]}
        />

        <InvoiceToolbar
          invoice={rapportagents?.data}
          handleInvoice={handleInvoice}
          value={fDate(value)}
          value1={fDate(value1)}
          value5={value5}
          companyname={user?.companyname}
          agentName={agent}
          numeroMachine={numeroMachine}
          tirage={tirage}
          userPhoto={user?.user_photo}
          isAgent="true"
        />

        <Card sx={{ pt: 5, px: 5 }}>
          <Grid container>
            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'left' }, width: 120, height: 50 }}>
                <img alt="LOGO" src={user?.user_photo} style={{ paddingLeft: 18, paddingRight: 18 }} />
              </Box>
            </Grid>
            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'right' } }} />
            </Grid>
            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'left' } }}>
                <Typography variant="h6">{user?.companyname}</Typography>
              </Box>
            </Grid>
            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'right' } }}>
                <Typography variant="h6">{fDateTime(today)}</Typography>
              </Box>
            </Grid>

            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Typography paragraph variant="overline" sx={{ color: 'text.disabled' }}>
                De
              </Typography>
              <Typography variant="body2">{fDate(value)}</Typography>
            </Grid>

            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Typography paragraph variant="overline" sx={{ color: 'text.disabled' }}>
                a
              </Typography>
              <Typography variant="body2">{fDate(value1)}</Typography>
            </Grid>

            <Grid item xs={12} sm={4} sx={{ mb: 5 }}>
              <Typography paragraph variant="overline">
                Tirage: {tirage}
              </Typography>
            </Grid>

            <Grid item xs={12} sm={4} sx={{ mb: 5 }}>
              <Typography paragraph variant="overline">
                Numero machine: {numeroMachine}
              </Typography>
            </Grid>

            <Grid item xs={12} sm={4} sx={{ mb: 5 }}>
              <Typography paragraph variant="overline">
                vendeur: {agent}
              </Typography>
            </Grid>
          </Grid>

          <Typography paragraph variant="overline">
            Rapports Details
          </Typography>

          <Scrollbar>
            <TableContainer sx={{ minWidth: 960 }}>
              <Table>
                <TableHead
                  sx={{
                    borderBottom: (theme) => `solid 1px ${theme.palette.divider}`,
                    '& th': { backgroundColor: 'transparent' }
                  }}
                >
                  <TableRow>
                    <TableCell align="left">Date</TableCell>
                    <TableCell align="left">Fiches</TableCell>
                    <TableCell align="left">F.Gagnants</TableCell>
                    <TableCell align="left">Montants</TableCell>
                    <TableCell align="left">Commissions</TableCell>
                    <TableCell align="left">Pertes</TableCell>
                    <TableCell align="left">Gains</TableCell>
                    <TableCell align="left">Depots</TableCell>
                    <TableCell align="left">Retraits</TableCell>
                    <TableCell align="left">Balances</TableCell>
                  </TableRow>
                </TableHead>

                <TableBody>
                  {rapportagents?.data?.map((row, index) => (
                    <TableRow
                      key={index}
                      sx={{
                        borderBottom: (theme) => `solid 1px ${theme.palette.divider}`
                      }}
                    >
                      <TableCell align="left">{row?.date}</TableCell>
                      <TableCell align="left">{row?.quantite}</TableCell>
                      <TableCell align="left">{row?.fgagnant}</TableCell>
                      <TableCell align="left">{parseFloat(row?.montant_total).toFixed(2)}</TableCell>
                      <TableCell align="left">{parseFloat(row?.commission).toFixed(2)}</TableCell>
                      <TableCell align="left">{parseFloat(row?.perte).toFixed(2)}</TableCell>
                      <TableCell align="left">
                        <Label
                          variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                          color={(row?.gain < 0 && 'error') || 'success'}
                        >
                          {parseFloat(row?.gain).toFixed(2)}
                        </Label>
                      </TableCell>
                      <TableCell align="left">{parseFloat(row?.depot).toFixed(2)}</TableCell>
                      <TableCell align="left">{parseFloat(row?.retrait).toFixed(2)}</TableCell>
                      <TableCell align="left">
                        <Label
                          variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                          color={(row?.balance < 0 && 'error') || 'success'}
                        >
                          {parseFloat(row?.balance).toFixed(2)}
                        </Label>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table>
                <TableBody>
                  <TableRow>
                    <TableCell
                      align="left"
                      style={{
                        borderBottom: '1px solid #000000',
                        color: '#000000'
                      }}
                    >
                      <Typography variant="h6">Total Fiche</Typography>
                    </TableCell>
                    <TableCell align="left" style={{ borderBottom: `1px solid ${theme.palette.primary.main}` }}>
                      <Typography variant="h6">{total4}</Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell
                      align="left"
                      style={{
                        borderBottom: '1px solid #000000',
                        color: '#000000'
                      }}
                    >
                      <Typography variant="h6">Total Fiche Gagnant</Typography>
                    </TableCell>
                    <TableCell align="left" style={{ borderBottom: `1px solid ${theme.palette.primary.main}` }}>
                      <Typography variant="h6">{total5}</Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell
                      align="left"
                      style={{
                        borderBottom: '1px solid #000000',
                        color: '#000000'
                      }}
                    >
                      <Typography variant="h6">Total Vente</Typography>
                    </TableCell>
                    <TableCell align="left" style={{ borderBottom: `1px solid ${theme.palette.primary.main}` }}>
                      <Typography variant="h6"> {fCurrency(total3)}</Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell
                      align="left"
                      style={{
                        borderBottom: '1px solid #000000',
                        color: '#000000'
                      }}
                    >
                      <Typography variant="h6">Total Commission</Typography>
                    </TableCell>
                    <TableCell align="left" style={{ borderBottom: `1px solid ${theme.palette.primary.main}` }}>
                      <Typography variant="h6"> {fCurrency(total2)}</Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell
                      align="left"
                      style={{
                        borderBottom: '1px solid #000000',
                        color: '#000000'
                      }}
                    >
                      <Typography variant="h6">Total a pay</Typography>
                    </TableCell>
                    <TableCell align="left" style={{ borderBottom: `1px solid ${theme.palette.primary.main}` }}>
                      <Typography variant="h6"> {fCurrency(total1)}</Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell
                      align="left"
                      style={{
                        borderBottom: '1px solid #000000',
                        color: '#000000'
                      }}
                    >
                      <Typography variant="h6">Profit ou perte</Typography>
                    </TableCell>
                    <TableCell align="left" style={{ borderBottom: `1px solid ${theme.palette.primary.main}` }}>
                      <Typography variant="h6"> {fCurrency(total)}</Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell
                      align="left"
                      style={{
                        borderBottom: '1px solid #000000',
                        color: '#000000'
                      }}
                    >
                      <Typography variant="h6">Total Depot</Typography>
                    </TableCell>
                    <TableCell align="left" style={{ borderBottom: `1px solid ${theme.palette.primary.main}` }}>
                      <Typography variant="h6"> {fCurrency(totaldepot)}</Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell
                      align="left"
                      style={{
                        borderBottom: '1px solid #000000',
                        color: '#000000'
                      }}
                    >
                      <Typography variant="h6">Total Retrait</Typography>
                    </TableCell>
                    <TableCell align="left" style={{ borderBottom: `1px solid ${theme.palette.primary.main}` }}>
                      <Typography variant="h6"> {fCurrency(totalretrait)}</Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell
                      align="left"
                      style={{
                        color: '#000000'
                      }}
                    >
                      <Typography variant="h6">Profit ou Perte apres depot et retrait</Typography>
                    </TableCell>
                    <TableCell align="left">
                      <Typography variant="h6"> {fCurrency(totalbalance)}</Typography>
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>
          </Scrollbar>

          <Divider sx={{ mt: 5 }} />

          <Grid container>
            <Grid item xs={12} md={9} sx={{ py: 3 }}>
              <Typography variant="subtitle2">NOTES</Typography>
              <Typography variant="body2">
                Ce rapport est gener a partir des fiches non supprimes sur le systeme!
              </Typography>
            </Grid>
          </Grid>
        </Card>
      </Container>
    </Page>
  );
}
